import React, { FC } from 'react';
import { graphql, Link, PageProps } from 'gatsby';

import { language, getLanguage } from '../../tools/Language';
import Navigation from 'components/Navigation';
import SEO from 'components/SEO';
import * as style from './index.module.scss';


type DataProps = {
    site: {
        siteMetadata: {
            languages: Array<language>
        }
    }
    allMarkdownRemark: {
        nodes: Array<{
            fields: {
                contents: string
                isRoot: boolean
                language: string
                path: string
            }
            frontmatter: {
                title: string
            }
        }>
    }
}
const ContentsTemplate: FC<PageProps<DataProps>> = ({ data, location }) => {
    const language = getLanguage(location.pathname, data.site.siteMetadata.languages);


    return (
        <div
            className={style.div}
        >
            <h1>{language.contents}</h1>
            <nav
                className={style.contents}
            >
                {
                    data.allMarkdownRemark.nodes
                        .filter(node => node.fields.isRoot)
                        .map((node, index) => (
                            <Link
                                key={`Link${index}`}
                                to={`${node.fields.path}`}
                                className={style.link}
                            >
                                <span>{node.frontmatter.title}</span>
                                <span
                                    className={style.articleQuantity}
                                >
                                    {
                                        data.allMarkdownRemark.nodes
                                            .filter(article => (article.fields.contents === node.fields.contents))
                                            .length
                                    }
                                </span>
                            </Link>
                        ))
                }
            </nav>

            <Navigation
                currentPath={location.pathname}
            />
        </div>
  )
}
export default ContentsTemplate;


export const query = graphql`
    query Contents($language: String!) {
        site {
            siteMetadata {
                languages {
                    id
                    path
                    contents
                }
            }
        }
        allMarkdownRemark(
            filter: {fields: {language: {eq: $language}}}
            sort: {frontmatter: {order: ASC}}
        ) {
            nodes {
                fields {
                    contents
                    isRoot
                    language
                    path
                }
                frontmatter {
                    title
                }
            }
        }
    }
`


export const Head: FC<PageProps> = ({ location }) => (
    <SEO
        pathname={location.pathname}
    />
)